.projects{
    &__header {
        font-size: 60px;
        font-style: italic;
        padding-bottom: 10px;
    }
}

.project {
    &__header{
        display: grid;
        grid-template-columns: 1fr 3fr;
        gap: 1rem;
        font-family: sans-serif;
         span {
            border-top:  1px solid #000;
            padding: 1rem;
            font-size: 30px;
            text-align: center;
        }
    }
    &__infos {
        margin-top: 1rem;
        &--name {
            position: relative;
            font-size: 3vw;
            font-family: sans-serif;
            align-items: center;
            font-weight: 400;
        }
    }
    &__img {
        position: relative;
        width: 100vw;
        height: 700px;
        img{
            width: 60vw;
            height: 600px;
            object-fit: cover;
            position: absolute;
            right: 10vw;
            top:3rem
        }
    }
    &__links {
        padding-top: 3rem;
        button {
            &::before {
                background-color: black;
            }
            &::after {
                display: none;
            }
        }
    }
    &__tags{
      position: absolute;
      bottom: 4rem;
      left: 3rem;
      font-size: 3rem;
      display: flex;
      flex-direction: column;  
      gap: 1rem;
    }
    .coolFunnyLink {
        position: relative;
        font-family: sans-serif;
        svg{
            position: absolute;
            bottom: 1;
            right:0;
            stroke-width: 3px;
            path{
                stroke-dasharray: 1;
                stroke-dashoffset: 1;
                transition: stroke-dashoffset 0.6s cubic-bezier(0.7,0,0.3,1);
            }
        }
        &:hover{
           svg path{
            stroke-dashoffset: 0;
            transition-timing-function: cubic-bezier(0.8, 1, 0.7, 1);
           }
        }
    }
}

@media (max-width: 1350px) {
    .projects {
        padding: 3rem;
    }
    .project__img img {
        width: 74vw;
        right: 5rem;
    }
    .project__infos--name {
        font-size: 38px;
    }
    .project .coolCircleEyeButton {
        transform:translate(-9rem, -5rem)
    }
}
@media (max-width:740px){
 .project {
    padding: 1rem;
 }   
 .project__tags {
    flex-direction: row;
    top: 0;
    left: 0;
 }
 .project__infos--name{
    font-size: 35px;
    span{
        display: none;
    }
 }
 .project__img img {
    width: 85vw;
    right: 3rem;
 }
}
@media (max-width: 440px){
.project .coolCircleEyeButton{
    transform: translate(0);
}
.project__tags{
    gap: 6px;
    font-size: 14px;
}
.project__header span{
    font-size: 25px;
}
}